import React, {Component} from 'react';
import {
	Box, 
	Heading,
	RoutedButton
} from 'grommet';

class Register extends Component {
	render ( ) {
		return (
			<Box
				flex
				direction='column'
				justify='center'
				align='center'
			>
				<Box
					width='20%'
					height='40%'
					direction='column'
					align='center'
					justify='between'
					pad='medium'
					border={{
						side: 'all',
						color: 'darker',
						size: 'medium'
					}}
					animation={{
						type: 'fadeIn',
						delay: 50,
						duration: 750,
						size: 'large'
					}}
					style={{
						borderRadius: '20px'
					}}
				>
					<Heading level='1'>Register</Heading>
					<RoutedButton
						path='/'
						hoverIndicator
						label={<strong>Enter</strong>}
						color='darker'
					>
					</RoutedButton>
				</Box>
			</Box>
		);
	}
}

export default Register;