import React, {Component} from 'react';
import {
	Box,
	RoutedButton,
	Heading,
	Text
} from 'grommet';

class Landing extends Component {
	render ( ) {
		return (
			<Box
				flex
				direction='column'
				justify='center'
				align='center'
			>
				<Box 
					animation={{
						type: 'slideDown',
						delay: 0,
						duration: 500,
						size: 'large'
					}}
				>
					<Heading level='1'>Sycamore Scheduler</Heading>
				</Box>
				<Box
					direction='row'
					align='center'
					justify='center'
					animation={{
						type: 'fadeIn',
						delay: 0,
						duration: 750,
						size: 'xlarge'
					}}
				>
					<RoutedButton
						path='/Register'
						hoverIndicator
						label={<Text size='large' style={{fontFamily: 'monospace'}}><strong>register</strong></Text>}
						margin={{
							right: 'medium'
						}}
					>
					</RoutedButton>
					<RoutedButton
						path='/SignIn'
						hoverIndicator
						label={<Text size='large' style={{fontFamily: 'monospace'}}><strong>sign in</strong></Text>}
						margin={{
							right: 'medium'
						}}
					>
					</RoutedButton>
					<RoutedButton
						path='/Main'
						hoverIndicator
						label={<Text size='large' style={{fontFamily: 'monospace'}}><strong>guest</strong></Text>}
					>
					</RoutedButton>
				</Box>
			</Box>
		);
	}
}

export default Landing;